import React from 'react';
import {
  Route,
  Link,
  HashRouter,
  Switch
} from "react-router-dom";

import { RelSymWasm } from './RelSymWasm';
import { Home } from './Home';

function App() {
  return (
    <HashRouter basename="/">
      <Switch>
        <Route path="/relsymwasm" component={RelSymWasm}/>
      <Route exact path="/" component={Home}/>
      </Switch>
    </HashRouter>
  );
}

export default App;
